}

@-webkit-keyframes myAnim {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  
    50% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

@keyframes myAnim {
    0% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  
    50% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
    }
  
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
